
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  /* background-color: #f1f1f1; */
  height: 100%;
  width: 100%;
}
@charset "utf-8";

* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

/* 禁用iPhone中Safari的字号自动调整 */

html {
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  /* 字体大小设置为 100px */
  font-size: 100px;
}

html,
body {
  width: 100%;
  height: 100%;
  background-color: #fff;
}

html {
  font-size: 0.12rem;
}

/* 去除iPhone中默认的input样式 */

/* input {
    -webkit-appearance: none;
    resize: none;
} */

/* 取消链接高亮  */

body,
div,
ul,
li,
ol,
h1,
h2,
h3,
h4,
h5,
h6,
/* input, */
textarea,
select,
p,
dl,
dt,
dd,
a,
img,
button,
form,
table,
th,
tr,
td,
tbody,
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  font-family: Microsoft PingFang;
}

/* 设置HTML5元素为块 */

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

*:focus {
  outline: none;
}

/* 清除输入框内阴影 */

/* input,
select,
textarea {
    border: 0;
    -webkit-appearance: none;
    appearance: none;
} */

/* 禁用长按页面时的弹出菜单(iOS下有效) ,img和a标签都要加 */

img,
a {
  -webkit-touch-callout: none;
  text-decoration: none;
}

/* 清零 */

body,
div,
ul,
li,
ol,
h1,
h2,
h3,
h4,
h5,
h6,
/* input, */
textarea,
select,
p,
dl,
dt,
dd,
a,
img,
button,
form,
table,
th,
tr,
td,
tbody,
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  margin: 0;
  padding: 0;
  border: none;
}

body {
  font-family: Tahoma, Arial, Roboto, "Droid Sans", "Helvetica Neue", "Droid Sans Fallback", "Heiti SC", sans-self;
  color: #666;
}

em,
i {
  font-style: normal;
}

strong {
  font-weight: normal;
}

.clearfix:after {
  content: "";
  display: block;
  visibility: hidden;
  height: 0;
  clear: both;
}

.clearfix {
  zoom: 1;
}

a {
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  -webkit-user-select: none;
  -moz-user-focus: none;
  -moz-user-select: none;
  text-decoration: none;
  font-family: Tahoma, Arial, Roboto, "Droid Sans", "Helvetica Neue", "Droid Sans Fallback", "Heiti SC", sans-self;
  

}

a,a:hover,a:active,a:visited,a:link,a:focus{

    -webkit-tap-highlight-color:rgba(0,0,0,0);
    
    -webkit-tap-highlight-color: transparent;
    
    outline:none;
    
    background: none;
    
    text-decoration: none;}

ul,
ol,
li {
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-family: Tahoma, Arial, Roboto, "Droid Sans", "Helvetica Neue", "Droid Sans Fallback", "Heiti SC", sans-self;
  font-weight: normal;
}

fieldset,
img {
  border: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

caption,
th {
  text-align: left;
}

img {
  display: block;
  height: auto;
  width: auto;
}

#app .zhucebox .van-overlay {
  background: rgba(0, 0, 0, .1);
}

#app .zijiabox .van-tabs__line {
  background-color: #ffe009;
}

#app  .van-swipe__indicators{
  display: none;
}

